import React from 'react'
import { Menu , ShoppingCart } from 'lucide-react';
import Link from 'next/link';
import {Popover, PopoverContent, PopoverTrigger,} from "@/components/ui/Popover";
const Navbar = () => {
    return (
        <nav className="flex justify-between items-center py-4 px-6 bg-white shadow-md">
            <div className="text-2xl font-bold">Bandage</div>
            <ul className="flex gap-4 text-gray-700">
                <li className="hover:text-blue-500 transition">
                    <a href="/">Home</a>
                </li>
                <li className="hover:text-blue-500 transition">
                    <a href="/shop">Shop</a>
                </li>
                <li className="hover:text-blue-500 transition">
                    <a href="/about">About</a>
                </li>
                <li className="hover:text-blue-500 transition">
                    <a href="/blog">Blog</a>
                </li>
                <li className="hover:text-blue-500 transition">
                    <a href="/contact">Contact</a>
                </li>
                <li className="hover:text-blue-500 transition">
                    <a href="/pages">Pages</a>
                </li>
            </ul>
            <div className="flex gap-4 items-center">
                <a href="/login" className="hover:text-blue-500 transition">
                    Login / Register
                </a>
                <button className="text-gray-700 hover:text-blue-500">
                    <i className="fa fa-search"></i>
                </button>
                <button className="text-gray-700 hover:text-blue-500">
                    <i className="fa fa-shopping-cart"></i>
                </button>
            </div>
    <Popover>
        <PopoverTrigger asChild>
            <button className="md:hidden focus:outline-none">
                <div className="w-8 h-8 flex items-center justify-center border rounded-md">
                    <span className="material-icons text-gray-800"><Menu/></span>
                </div>
            </button>
        </PopoverTrigger>
        <PopoverContent className="p-4 bg-white shadow-lg rounded-lg w-56">
            <div className="space-y-4">
                <button
                    className={`block w-full text-left px-4 py-2 rounded-lg ${getButtonClass(
                        "home"
                    )} hover:bg-blue-600`}
                >
                    Home
                </button>
                <Link href="/category"
                      className={`block w-full text-left px-4 py-2 rounded-lg ${getButtonClass(
                          "category"
                      )} hover:bg-gray-300`}
                >
                    Category
                </Link>

                <Link href="/about"
                      className={`block w-full text-left px-4 py-2 rounded-lg ${getButtonClass(
                          "login"
                      )} hover:bg-gray-300`}
                >
                    about
                </Link>
                <button
                    className={`block w-full text-left px-4 py-2 rounded-lg ${getButtonClass(
                        "cart"
                    )} hover:bg-gray-300`}
                >
                    <ShoppingCart/>
                </button>
            </div>
        </PopoverContent>
    </Popover>
        </nav>
    )
}
export default Navbar
